*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  height: 100%;
  position: relative;
}

.add-img {
  width: 20px;
  height: 20px;
}

.add-new-note {
  color: #eee;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 140px;
  padding: 1%;
}

.Notes {
  height: 100vh;
}
